body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  min-width: 1100px;
  overflow: scroll;
}

.search-form {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-top: 25px;
}

.input-field {
    width: 400px;
}

.search-button {
    margin: 25px;
}

.preloader {
    width: 30px;
    height: 30px;
}

.cardClass {
    display: flex;
    flex-direction: row;
    /* justify-content: center; */
    align-items: center;
    /* justify-content: flex-start; */
}

.sumInfo {
    display: flex;
    flex-direction: row;
    align-items: center;
}

h5 {
    margin: 0;
}

.champion {
    margin: 0 10px;
}

.spell-runes {
    display: flex;
    flex-direction: column;
    margin: 10px;
}

.teams {
    display: flex;
    align-items: center;
    margin: 0 10px;
}

.team {
    display: flex;
    flex-direction: column;
    /* align-items: flex-start; */
    margin: 0 20px;
    width: 195px;
}

.items {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 0 5px;
}

.stats {
    margin: 0 10px;
}

.player > img {
    width: 40px;
    height: 40px;
}

.player {
    display: flex;
    margin: 2px;
}

.player-names {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-left: 7px;
}

.vs {
    margin-right: 10px;
}

